body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.navbar {
    background-color:#96939B;
}

#title {
    font-size: 55px;
    color:white;
    font-family: 'Playfair Display', serif;
}
.item{
    font-size:25px;
    color:black;
}
.navbar li {
    display: inline-block;
    padding: 20px 0;
    margin:5px;
    text-align: left;
   
};




#description {
    font-size: 40px;
    font-weight:bold;
    font-family: 'Playfair Display', serif;
}

#jumbo {
    background-color:#B8DBD9;
    text-align:center;
    border:solid 1px black;
}

#search-container{
    text-align: center;
    border: solid black 1px;
    padding:5px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.card{
    border:solid black 1px;
    background-color: lavender;
    min-width:350px;
    min-height: 175px;
    z-index: inherit 400;
}
#results-container{
    border: black solid 1px;
    min-width: 400px;
    min-height: 400px;
    padding:5;
    margin-top: 15px;
    background: rgb(217, 218, 217);
    font-family: 'Playfair Display', serif;
}
#saved-container{
    border: black solid 1px;
    min-width: 400px;
    min-height: 400px;
    background-color:lightblue;
    padding:5;
    margin-top: 15px;
    font-family: 'Playfair Display', serif;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

